n        .select select\n          border-radius: 0\n      &:first-child:not(:only-child)\n        .button,\n        .input,\n        .select select\n          +ltr\n            border-bottom-right-radius: 0\n            border-top-right-radius: 0\n          +rtl\n            border-bottom-left-radius: 0\n            border-top-left-radius: 0\n      &:last-child:not(:only-child)\n        .button,\n        .input,\n        .select select\n          +ltr\n            border-bottom-left-radius: 0\n            border-top-left-radius: 0\n          +rtl\n            border-bottom-right-radius: 0\n            border-top-right-radius: 0\n      .button,\n      .input,\n      .select select\n        &:not([disabled])\n          &:hover,\n          &.is-hovered\n            z-index: 2\n          &:focus,\n          &.is-focused,\n          &:active,\n          &.is-active\n            z-index: 3\n            &:hover\n              z-index: 4\n      &.is-expanded\n        flex-grow: 1\n        flex-shrink: 1\n    &.has-addons-centered\n      justify-content: center\n    &.has-addons-right\n      justify-content: flex-end\n    &.has-addons-fullwidth\n      .control\n        flex-grow: 1\n        flex-shrink: 0\n  &.is-grouped\n    display: flex\n    justify-content: flex-start\n    & > .control\n      flex-shrink: 0\n      &:not(:last-child)\n        margin-bottom: 0\n        +ltr-property(\"margin\", 0.75rem)\n      &.is-expanded\n        flex-grow: 1\n        flex-shrink: 1\n    &.is-grouped-centered\n      justify-content: center\n    &.is-grouped-right\n      justify-content: flex-end\n    &.is-grouped-multiline\n      flex-wrap: wrap\n      & > .control\n        &:last-child,\n        &:not(:last-child)\n          margin-bottom: 0.75rem\n      &:last-child\n        margin-bottom: -0.75rem\n      &:not(:last-child)\n        margin-bottom: 0\n  &.is-horizontal\n    +tablet\n      display: flex\n\n.field-label\n  .label\n    font-size: inherit\n  +mobile\n    margin-bottom: 0.